import React, { Component } from 'react'
import { NavBar, Icon } from 'antd-mobile'

import ReactSwipe from 'react-swipe';


export default class School extends Component {

  constructor() {
    super();
    this.state = {
      index: 0
    }
  }
  render() {
    let opt = {
      callback: function (index) {
        console.log(index);
        this.setState({ index: Number(index) })
      }.bind(this)
    }

    return (
      <div id="home-category">
        <ReactSwipe className="carousel" swipeOptions={opt}>
          <div className="carousel-item">
            <ul className='clear-fix'>
              <li className="float-left ktv"><img style={{width:"100%"}} src={require('../../assets/images/banner01.png')} alt=""/></li>
            </ul>
          </div>
          <div className="carousel-item">
            <ul className='clear-fix'>
              <li className="float-left meishi"><img style={{ width: "100%" }} src={require('../../assets/images/banner02.jpg')} alt="" /></li>
            </ul>
          </div>
          <div className="carousel-item">
            <ul className='clear-fix'>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican"><button>sss </button></li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
              <li className="float-left xican">SPA</li>
            </ul>
          </div>
        </ReactSwipe>
        <div className="index-container">
          <ul>
            <li className={this.state.index === 0 ? 'selected' : ''}></li>
            <li className={this.state.index === 1 ? 'selected' : ''}></li>
            <li className={this.state.index === 2 ? 'selected' : ''}></li>
          </ul>
        </div>
      </div>
    )
  }
/*   render() {
    return (
      <div>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          style={{ background: "#67C23A" }}
          onLeftClick={() => { this.props.history.push('/') }}
        >我的学校</NavBar>
      </div>
    )
  } */
}
