import React, { Component } from 'react'
import { SearchBar, NavBar, Icon, Tag, WingBlank } from 'antd-mobile'
import './search.sass'

export default class Search extends Component {
    render() {
        return (
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    style={{ background: "#67C23A" }}
                    onLeftClick={() => { this.props.history.push('/') }}
                >启课教育</NavBar>
                <SearchBar placeholder="输入要查找的课程" ref={ref => this.autoFocusInst = ref} />
                <WingBlank size="md">
                    <h4>搜索发现</h4>
                    <div className="tagbox">
                        <Tag>国王的新衣</Tag>
                        <Tag>新概念英语青少版</Tag>
                        <Tag>剑桥少儿英语</Tag>
                        <Tag>开心作业三年级</Tag>
                        <Tag>小学奥数初级班</Tag>
                        <Tag>同步数学</Tag>
                    </div>
                </WingBlank>
            </div>
        )
    }
}
