import React, { Component } from 'react'
import { NavBar, Icon, Tag, WingBlank } from 'antd-mobile'
// 引入json数据文件
import city from '../../json/city.json'
import './citylist.sass'

export default class cityList extends Component {
    render() {
        return (
            <div className='citylistbox' style={{ marginTop: '45px', background: '#F5F5F9' }}>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    style={{ background: "#67C23A" }}
                    onLeftClick={() => { this.props.history.push('/') }}
                >城市列表</NavBar>
                <WingBlank size='lg'>
                {/* 热门城市 */}
                    <h4>热门城市</h4>
                    {
                        city.hotcity.map(obj => (
                            <Tag key={obj}>{obj}</Tag>
                        ))
                    }
                    {/* 城市列表 */}
                    <h4>城市列表</h4>
                    {
                        city.city.map(obj => {
                            return <div key={obj.title}>
                                <h4>{obj.title}</h4>
                                {
                                    obj.lists.map(val => <Tag key={val}>{val}</Tag>)
                                }
                            </div>
                        })
                    }

                </WingBlank>
            </div>
        )
    }
}
