import React, { Component } from 'react'
import { NavBar, Icon } from 'antd-mobile'


export default class Source extends Component {
  render() {
    return (
      <div>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          style={{ background: "#67C23A" }}
          onLeftClick={() => { this.props.history.push('/') }}
        >资源中心</NavBar>
      </div>
    )
  }
}
