import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './mylist.sass'
import { connect } from 'react-redux';
import { saveTracks } from '../../store/actions'


class Mylist extends Component {
    render() {
        return (
            <div>
               {
                    this.props.listdata.map(obj => (
                        <div className='contentbox' key={obj.id} onClick={this.savetracks.bind(this,obj)} style={{ padding: '.5rem 1.5rem', borderBottom: '1px solid #eee' }}>
                            <Link to={`/readinglist?id=${obj.id}`}>
                                {/* 标题 */}
                                <div
                                    style={{
                                        lineHeight: '2.8rem',
                                        color: '#888',
                                        fontSize: "1.2rem",
                                        borderBottom: '1px solid #F6F6F6',
                                        fontWeight: 'bold'
                                    }}
                                >{obj.name}</div>
                                {/* 内容 */}
                                <div style={{ display: '-webkit-box', display: 'flex', padding: '15px 0' }}>
                                    <img style={{ height: '6rem', marginRight: '15px' }} src={require(`../../assets/images/list/${obj.src}`)} alt="" />
                                    <div style={{ lineHeight: 1.5 }}>
                                        <div style={{ marginBottom: '.5rem', fontSize: "1.1rem", textIndent: '2rem' }}>{obj.content}</div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    ))
               }
               <p style={{color:"#999",textAlign:'center',lineHeight:'4rem'}}>到底啦...</p>
            </div>
        )
    }
    savetracks(obj){
        this.props.dispatch(saveTracks(obj))
    }
}

export default connect()(Mylist)
