import React, { Component } from 'react'
import { HashRouter, Route, Switch } from 'react-router-dom';
// 引入store
import store from '../store/store'
// 引入producer
import { Provider } from 'react-redux'

import Login from './login/Login'
import Reg from './reg/Reg'
import Nav from './nav/Nav'
import Map from './map/Map'
// 主页宫格对应组件
import Fees from './fees/Fees'
import Grown from './grown/Grown'
import Homework from './homework/Homework'
import Reading from './reading/Reading'
import School from './school/School'
import Score from './score/Score'
import Source from './source/Source'
import Teacher from './teacher/Teacher'
// 我的页面对应组件
import Tracks from './tracks/Tracks'
// 城市列表
import cityList from './citylist/cityList'
// 搜索
import Search from './search/Search'
// 阅读
import Readinglist from './readinglist/Readinglist'
import Error404 from './error404/Error404'

export default class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <HashRouter>
          <Switch>
            {/* 主页 */}
            <Route path='/' exact component={Nav} />
            {/* 登录 */}
            <Route path='/login' component={Login} />
            {/* 注册 */}
            <Route path='/reg' component={Reg} />
            {/* 地图 */}
            <Route path='/map' component={Map} />
            <Route path='/fees' component={Fees} /> {/* 续费 */}
            <Route path='/grown' component={Grown} /> {/* 成长 */}
            <Route path='/homework' component={Homework} /> {/* 作业 */}
            <Route path='/reading' component={Reading} /> {/* 阅读 */}
            <Route path='/school' component={School} /> {/* 学校 */}
            <Route path='/score' component={Score} /> {/* 积分 */}
            <Route path='/source' component={Source} /> {/* 资源 */}
            <Route path='/teacher' component={Teacher} /> {/* 老师 */}
            <Route path='/search' component={Search} /> {/* 搜索 */}
            <Route path='/readinglist' component={Readinglist} /> {/* 阅读列表 */}
            <Route path='/tracks' component={Tracks} /> {/* 足迹 */}
            <Route path='/citylist' component={cityList} /> {/* 城市列表 */}
            {/* 404 */}
            <Route component={Error404} />
          </Switch>
        </HashRouter>
      </Provider>
    )
  }
}
