import React, { Component } from 'react'
import { Flex, WhiteSpace, WingBlank, InputItem, Checkbox, NavBar, Icon, Button, Toast } from 'antd-mobile'
import { Link } from 'react-router-dom'
import './reg.sass'
import {reg} from '../../api/api'
const AgreeItem = Checkbox.AgreeItem

export default class Reg extends Component {
    constructor(){
        super();
        this.state={
            acc:'',
            pwd:'',
            codenum:''
        }
    }
    render() {
        return (
            <div className='regbox'>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    style={{ background: "#67C23A" }}
                    onLeftClick={this.Back.bind(this)}
                >快速注册</NavBar>
                {/* 注册列表 */}
                <WhiteSpace size="lg" />
                <WingBlank size="lg">
                    <InputItem
                        placeholder="请输入手机"
                        value={this.state.acc}
                        onChange={val => { this.setState({ acc:val})}}
                        clear
                    />
                    <WhiteSpace size="sm" />
                    <InputItem
                        placeholder="请输入密码"
                        value={this.state.pwd}
                        onChange={val => { this.setState({ pwd:val})}}
                        type='password'
                        clear
                    />
                    <WhiteSpace size="sm" />
                    <InputItem
                        placeholder="请输入验证码"
                        value={this.state.codenum}
                        onChange={val => { this.setState({ codenum: val }) }}
                        clear
                        extra='获取验证码'
                    />
                   
                    <WhiteSpace size="sm" />
                    <Flex>
                        <Flex.Item>
                            <AgreeItem data-seed="logId" onChange={e => console.log('checkbox', e)}>
                                <div style={{ fontSize: "1.1rem", marginTop: ".2rem" }}>
                                    我已同意
                                <a onClick={(e) => { e.preventDefault(); alert('agree it'); }}>《用户服务协议》</a>
                                    及
                                <a onClick={(e) => { e.preventDefault(); alert('agree it'); }}>《隐私权政策》</a>
                                </div>
                            </AgreeItem>
                        </Flex.Item>
                    </Flex>
                    <WhiteSpace size="sm" />
                    <Button style={{ background: "#67C23A", color: "#fff",fontSize:'1.5rem' }} onClick={this.addaccount.bind(this)}>注册</Button>
                    <WhiteSpace size="sm" />
                    <Link to='/login'>已有账号</Link>
                </WingBlank>


            </div>
        )
    }
    Back() {
        this.props.history.push('/login')
    }
    //注册账号
    addaccount(){
        reg(this.state.acc,this.state.pwd).then(res=>{
            Toast.loading('注册中...', 1, () => {
                if (res.data === 'ok') {
                    Toast.success('注册成功，请登录', 1);
                    this.props.history.push('/login')
                }else if(res.data==='fail'){
                    Toast.fail('注册失败', 1);
                }
            });
        })       
    }
}
