import React, { Component } from 'react'
import { TabBar } from 'antd-mobile'
// 子组件的导入
import Chat from './chat/Chat'
import Collection from './collection/Collection'
import Default from './default/Default'
import My from './my/My'


export default class Nav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'main',
            tablist: [
                { title: '首页', key: "main", icon: 'icon-default1.png', selectedIcon: 'icon-default2.png' },
                { title: '聊天', key: "chat", icon: 'icon-chat1.png', selectedIcon: 'icon-chat2.png' },
                { title: '收藏', key: "collection", icon: 'icon-like1.png', selectedIcon: 'icon-like2.png' },
                { title: '我的', key: "my", icon: 'icon-my1.png', selectedIcon: 'icon-my2.png' },
            ]
        };
    }

    renderContent() {
        switch (this.state.selectedTab) {
            case 'main': return <Default />;
            case 'chat': return <Chat />;
            case 'collection': return <Collection />;
            case 'my': return <My/>;
        }
    }
    render() {
        return (
            <div>
                <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="#67C23A"
                        barTintColor="white"
                        prerenderingSiblingsNumber={0}
                    >
                        {
                            this.state.tablist.map(obj => (
                                <TabBar.Item
                                    title={obj.title}
                                    key={obj.key}
                                    icon={<div style={{
                                        width: '22px',
                                        height: '22px',
                                        background: `url(${require('../../assets/images/'+obj.icon)}) center center /  21px 21px no-repeat`
                                    }}
                                    />
                                    }
                                    selectedIcon={<div style={{
                                        width: '22px',
                                        height: '22px',
                                        background: `url(${require('../../assets/images/'+obj.selectedIcon)}) center center /  21px 21px no-repeat`
                                    }}
                                    />
                                    }
                                    selected={this.state.selectedTab ===  obj.key }
                                    onPress={() => {
                                        this.setState({
                                            selectedTab: obj.key,
                                        });
                                    }}
                                >
                                    {this.renderContent()}
                                </TabBar.Item>
                            ))
                        }
                    </TabBar>
                </div>
            </div>
        )
    }
}
