import React, { Component } from 'react'
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile'
// 引入自定义阅读列表
import Mylist from '../../../components/mylist/Mylist'


class Collection extends Component {

    getlist(){
        if (this.props.readinglist.length>0) {         
            return <Mylist listdata={this.props.readinglist} />
        } else {
            return (<div style={{ fontSize:'1.3rem',color:'#999',textAlign:'center',lineHeight:'5rem'}}>暂无数据...</div>)
        }
    }
    render() {
       
        
        return (
            <div style={{ marginTop: '45px', background:'#F5F5F9'}}>
                <NavBar
                    mode="dark"
                    style={{ background: "#67C23A" }}
                >我的收藏</NavBar>
                {this.getlist()}
            </div>
        );
    }
}

// 数据注入
export default connect(state=>{
    // return的值，会被注入到组件的props身上
    return{
        readinglist: state.readinglist
    }
})(Collection)
