import React, { Component } from 'react'
import { Flex, WhiteSpace, WingBlank, InputItem, Button, Toast } from 'antd-mobile'
import { Link } from 'react-router-dom'
import './login.sass'
import { login } from '../../api/api';
import { connect } from 'react-redux'
// 保存当前账号信息
import { saveLogin} from '../../store/actions'


class Login extends Component {
    constructor() {
        super();
        this.state = {
            acc: '',
            pwd: ''
        }
    }
    render() {
        return (
            <div className='logincontainer'>
                <div className='loginbox'>
                    <WhiteSpace size="xl" />
                    <WhiteSpace size="xl" />
                    <Flex justify="center">
                        <img src={require('../../assets/images/logo-qk-g.png')} alt='' />
                    </Flex>
                    <WhiteSpace size="xl" />
                    <WingBlank size="lg">
                        <InputItem
                            placeholder="输入手机号"
                            clear
                            type='text'
                            labelNumber={3}
                            value={this.state.acc}
                            onChange={val => { this.setState({ acc: val }) }}
                        >
                            <div style={{ backgroundImage: `url('${require('../../assets/images/icon-pwd.png')}')`, backgroundSize: 'cover', height: '22px', width: '22px' }} />
                        </InputItem>
                        <WhiteSpace size="lg" />
                        <InputItem
                            placeholder="输入密码"
                            type='password'
                            clear
                            value={this.state.pwd}
                            onChange={val => { this.setState({ pwd: val }) }}
                            labelNumber={3}
                        >
                            <div style={{ backgroundImage: `url('${require('../../assets/images/icon-user.png')}')`, backgroundSize: 'cover', height: '22px', width: '22px' }} />
                        </InputItem>
                        <WhiteSpace size="lg" />
                        <WhiteSpace size="lg" />
                        <Button onClick={this.loginFn.bind(this)} style={{ background: "#67C23A", color: "#fff" }}>登录</Button>
                        <WhiteSpace size="lg" />
                        <div className='linkbox'>
                            <Flex justify="between">
                                <Link to='/'>忘记密码?</Link>
                                <Link to='/reg'>快速注册</Link>
                            </Flex>
                        </div> 
                    </WingBlank>
                </div>
                <div className='tips'>登陆/注册即代表同意《启课教育用户协议》</div>
            </div>
        )
    }
    loginFn() {
        login(this.state.acc, this.state.pwd).then(res => {
            Toast.loading('登录中...', 1, () => {
                if (res.data === 'ok') {
                    this.props.history.push('/');
                    // 保存登录者的身份
                    this.props.dispatch(saveLogin(this.state.acc))
                    Toast.success('登录成功!', 1);
                } else if (res.data === 'fail') {
                    Toast.fail('用户名或密码错误', 1);
                }
            });
        }).catch(err => {
            console.log(err);
        })
    }
}

export default connect()(Login)
