import React, { Component } from 'react'
import { NavBar, Icon } from 'antd-mobile'
import { getcontent} from '../../api/api'
import './readinglist.sass'
import { connect } from 'react-redux';
// 引入通知
import { saveReadingList, saveTracks} from '../../store/actions'

class Readinglist extends Component {
  constructor() {
    super();
    this.state = {
      datalist: { src:'loading.gif'},
      src: 'icon-list-sc'
    }
  }
  componentDidMount(){
    // 获取id参数
    let idnum=window.location.hash;
    idnum=idnum.split('=')[1];
    getcontent(idnum).then(res=>{
      this.setState({
        datalist:res.data
      })
      // 保存当前数据进足迹
      this.props.dispatch(saveTracks(res.data));
      // 转成布尔
      this.state.datalist.iscollect = Boolean(this.state.datalist.iscollect);
      // 初始化收藏值
      this.state.datalist.iscollect = !this.state.datalist.iscollect;      
    })
    
    console.log(this.props.tracks);
    
  }
  render() {
    let {datalist}=this.state;
    return (
      <div className='contentbox' style={{width:'100%'}}>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          style={{ background: "#67C23A" }}
          onLeftClick={() => { this.props.history.push('/') }}
        >{datalist.name}</NavBar>
        <img style={{ width: '100%',marginTop:'45px' }} src={require(`../../assets/images/list/${datalist.src}`)} alt=""/>
        <div className='timebox'>
          <p>{datalist.updatetime}</p>
          <div className='scbox' onClick={this.collectfn.bind(this)}>{datalist.iscollect == 0 ? '收藏' : '已收藏'}<img src={require(`../../assets/images/${datalist.iscollect == 0 ? 'icon-list-sc' :'icon-list-sc-s'}.png`)} alt=""/></div>
         
        </div>
        <p style={{fontSize:'1.3rem',lineHeight:'2.4rem',textIndent:'2em',padding:'1rem',marginBottom:'2rem'}}>{datalist.innertext}</p>
      </div>
    )
  }
  // 收藏点击
  collectfn(){
    let { datalist}=this.state;
    // 判断值是否在readinglist数组中
    let { readinglist}=this.props;
    let flag=readinglist.some(obj => obj.id === datalist.id);
    // 取反当前的收藏状态
    datalist.iscollect = !datalist.iscollect;
    
    // 如果在数组中说明该条信息已经被收藏过
    this.setState({
      datalist: datalist
    })
    
    if (!flag){
      // 未被收藏，就发出通知
      this.props.dispatch(saveReadingList(datalist));
    }else{
      datalist.iscollect=true;
    }
    
    
  }
}

// 数据注入
export default connect(state=>{
  return {
    readinglist: state.readinglist,//收藏
    tracks: state.tracks//足迹
  }
})(Readinglist)
