import React, { Component } from 'react'
import './my.sass'
import { Grid, Button, WingBlank, WhiteSpace } from 'antd-mobile'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'


// 我的选项数据
const mydata = [
  { icon: 'icon-vip', text: 'VIP特权' },
  { icon: 'icon-course', text: '我的课程' },
  { icon: 'icon-collect', text: '我的收藏' },
  { icon: 'icon-footer', text: '我的足迹' },
  { icon: 'icon-money', text: '兑换日志' },
  { icon: 'icon-shopcar', text: '购买记录' },
  { icon: 'icon-ord', text: '我的订阅' },
  { icon: 'icon-service', text: '在线客服' },
  { icon: 'icon-Settings', text: '设置' },
].map((obj) => ({
  icon: require(`../../../assets/images/myicon/${obj.icon}.png`),
  text: obj.text
}));

class My extends Component {
  constructor() {
    super();
    this.state = {
      mydata: mydata
    }
  }
  componentDidMount(){
    console.log(this.props);
    
  }
  render() {
    return (
      <div className='mybox'>
        {/* 头像 */}
        <div className='avatarbox'>
          <img src={require("../../../assets/images/avatar.jpg")} alt="" />
          <p>{this.props.loginUser}</p>
        </div>
        {/* 状态值 */}
        <ul className="myinfo">
          <li>智慧币：
              <img src={require('../../../assets/images/icon-money.png')} alt="" />
            <span style={{ color: '#67C23A', fontWeight: 'bold' }}>99</span>个</li>
          <li>
            <Link to='/login'>
              <img src={require('../../../assets/images/icon-charge.png')} alt="" />
              <span>在线充值</span>
            </Link>
          </li>
        </ul>
        {/* 我的选项宫格 */}
        <Grid data={this.state.mydata}
          columnNum={3}
          renderItem={dataItem => (
            <Link to='tracks'>
              <div style={{ padding: '.8rem' }}>
                <img src={dataItem.icon} style={{ width: '30%', height: '30%' }} alt="" />
                <div style={{ color: '#888', fontSize: '1.2rem', marginTop: '.4rem' }}>
                  <span>{dataItem.text}</span>
                </div>
              </div>
            </Link>

          )}
        />
        {/* 注销 */}
        <WhiteSpace size="lg" />
        <WingBlank size="lg">
          <Button type="warning">注销</Button>
        </WingBlank>
        <WhiteSpace size="lg" />
        <WhiteSpace size="lg" />
      </div>
    )
  }
}


export default connect(state=>{
  return {
    loginUser: state.loginUser
  }
})(My)
